body {
  margin: 0;
  font-family: "Source Code Pro", monospace;
  background-color: #000;
  /* black base */
  color: #f1f1f1;
}

/* Container */
.home-container {
  padding: 40px 5%;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

/* Hero Section */
.hero {
  text-align: center;
  animation: fadeIn 1s ease;
}

.hero-title {
  font-size: 3.5rem;
  color: #e5e6e6;
  /* cyan */
  margin-bottom: 10px;
  font-weight: bold;
}

.hero-subtitle {
  color: #39FF14;
  /* electric yellow */
  font-size: 1.2rem;
  margin-top: 0;
  max-width: 600px;
  margin-left: auto;
  margin-right: auto;
}

/* About Section */
.about-section {
  background-color: #111;
  padding: 25px;
  border-left: 5px solid #39FF14;
  border-radius: 10px;
  margin: 50px auto;
  max-width: 800px;
  box-shadow: 0 0 15px rgba(57, 255, 20, 0.2);

}

.about-section h2 {
  color: #eee9e8;
  /* coral */
  font-size: 1.5rem;
  margin-bottom: 10px;
}

.about-section p {
  color: #dddddd;
  font-size: 1rem;
  line-height: 1.6;
}

/* Services Section */
.services-section {
  text-align: center;
  margin-top: 30px;
}

.services-section h2 {
  color: #e9edee;
  font-size: 1.8rem;
  margin-bottom: 20px;
}

.services-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(230px, 1fr));
  gap: 25px;
  padding: 0 10px;
}

.service-card {
  background-color: #111;
  border: 2px solid #222;
  padding: 20px;
  border-radius: 10px;
  transition: transform 0.3s ease;
  text-align: left;
}

.service-card:hover {
  transform: translateY(-5px);
  border-color: #39FF14;
  box-shadow: 0 0 15px rgba(57, 255, 20, 0.2);

}

.service-card h3 {
  color: #39FF14;
  font-size: 1.1rem;
  margin-bottom: 10px;
}

.service-card p {
  color: #ccc;
  font-size: 0.95rem;
}

/* Connect Button */
/* Connect Button */
.button-container {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
  margin-bottom: 30px;
}

.connect-button {
  background: linear-gradient(135deg, #00bcd4, #faff00);
  color: #000;
  padding: 14px 30px;
  border-radius: 10px;
  font-weight: 600;
  font-size: 1rem;
  text-decoration: none;
  transition: all 0.3s ease-in-out;
  box-shadow: 0 0 15px rgba(0, 188, 212, 0.5);
  max-width: 240px;
  /* prevents it from stretching full width */
  width: fit-content;
  /* takes up only the space it needs */
  white-space: nowrap;
  /* keeps text on one line */
  border: none;
  cursor: pointer;
}

.connect-button:hover {
  background: linear-gradient(135deg, #faff00, #00bcd4);
  transform: scale(1.05);
  color: #111;
}


/* Animation */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(30px);
  }

  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Responsive */
@media (max-width: 768px) {
  .hero-title {
    font-size: 2.5rem;
  }

  .hero-subtitle {
    font-size: 1rem;
  }

  .connect-button {
    padding: 12px 20px;
    font-size: 0.95rem;
  }
}

